function Subscriptions() {
  const encryptionKey = process.env.REACT_APP_ENCRYPTION_KEY;
  const encryptedToken = Cookies.get("token");
  const token = encryptedToken
    ? CryptoJS.AES.decrypt(encryptedToken, encryptionKey).toString(
      CryptoJS.enc.Utf8
    )
    : null;

  const [subscriptions, setSubscriptions] = useState([]);
  const [filteredSubscriptions, setFilteredSubscriptions] = useState([]);
  const [selectedSubscription, setSelectedSubscription] = useState(null);
  const [pauseDate, setPauseDate] = useState(null);
  const [isModalVisible, setIsModalVisible] = useState(false);
  const [searchText, setSearchText] = useState("");
  const [dateRange, setDateRange] = useState([null, null]);

  const encryptedUser = Cookies.get("user");
  const encryptedClube = Cookies.get("clubuser");

  const user =
    JSON.parse(
      CryptoJS.AES.decrypt(encryptedUser, encryptionKey).toString(
        CryptoJS.enc.Utf8
      )
    ) ||
    JSON.parse(
      CryptoJS.AES.decrypt(encryptedClube, encryptionKey).toString(
        CryptoJS.enc.Utf8
      )
    );

  const getSubscriptions = async () => {
    try {
      const res = await axios.get(
        "https://majesticmall.net/api/user/getAllSubscriptionsbyuserid/" +
        user._id,
        {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        }
      );
      if (res.status === 200) {
        setSubscriptions(res.data.success);
        setFilteredSubscriptions(res.data.success);
      }
    } catch (error) {
      console.error("Error fetching subscriptions:", error);
    }
  };
  useEffect(() => {

    getSubscriptions();
  }, []);

  useEffect(() => {
    filterSubscriptions();
  }, [searchText, dateRange, subscriptions]);

  const filterSubscriptions = () => {
    let filtered = subscriptions;

    if (searchText) {
      filtered = filtered.filter(
        (subscription) =>
          subscription._id.toLowerCase().includes(searchText.toLowerCase()) ||
          subscription.status
            .toLowerCase()
            .includes(searchText.toLowerCase()) ||
          subscription.productId.productname
            .toLowerCase()
            .includes(searchText.toLowerCase())
      );
    }

    if (dateRange && dateRange[0] && dateRange[1]) {
      filtered = filtered.filter((order) => {
        const orderDate = new Date(order?.startDate);
        const startDate = new Date(dateRange[0]);
        const endDate = new Date(dateRange[1]);
        startDate.setHours(0, 0, 0, 0);
        endDate.setHours(23, 59, 59, 999);

        return orderDate >= startDate && orderDate <= endDate;
      });
    }

    setFilteredSubscriptions(filtered);
  };

  const handlePause = async () => {
    try {
      const response = await axios.post(
        "https://majesticmall.net/api/user/pauseSubscription",
        {
          subscriptionId: selectedSubscription._id,
          pauseDate: pauseDate ? pauseDate.format("YYYY-MM-DD") : null,
        },
        {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        }
      );
      if (response.status === 200) {
        message.success("Subscription paused successfully");
        getSubscriptions();
        setIsModalVisible(false);
      }
    } catch (error) {
      console.error("Error pausing subscription:", error);
      message.error("Failed to pause subscription");
    }
  };

  const [balance, setBalance] = useState(0);
  const fetchWalletData = async () => {
    try {
      const response = await axios.get(
        `https://majesticmall.net/api/user/getwallet/${user?._id}`,
        {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        }
      );
      setBalance(response.data.success.balance);

    } catch (error) {
      console.error("Error fetching wallet data:", error);
    }
  };
  useEffect(() => {
    if (token) {
      fetchWalletData();
    }
    //  toast.error("Please login.");
  }, [token]);

  const handleRemove = async (pauseDate) => {
    try {
      const response = await axios.post(
        "https://majesticmall.net/api/user/removePauseDate",
        {
          subscriptionId: selectedSubscription._id,
          pauseDate: pauseDate,
        },
        {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        }
      );
      if (response.status === 200) {
        message.success("Subscription remove paused successfully");
        getSubscriptions();
        setIsModalVisible(false);
      }
    } catch (error) {
      console.error("Error pausing subscription:", error);
      message.error("Failed to remove pause subscription");
    }
  }

  const handleContinue = async (item) => {
    if (balance < item?.productValue) {
      message.error("Insufficient balance to continue subscription");
      return;
    }

    try {
      const response = await axios.post(
        "https://majesticmall.net/api/user/updateOrderStatus1",
        {
          orderId: item?._id, status: "Approved", remarks: "Manual Continue by customer",
        },
        {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        }
      );
      if (response.status === 200) {
        message.success("Subscription approved successfully");
        getSubscriptions();
        setIsModalVisible(false);
      }
    } catch (error) {
      console.error("Error pausing subscription:", error);
      message.error("Failed to remove pause subscription");
    }
  }
  const showModal = (subscription) => {
    setSelectedSubscription(subscription);
    setIsModalVisible(true);
  };

  const handleCancel = () => {
    setIsModalVisible(false);
    setSelectedSubscription(null);
    setPauseDate(null);
  };

  const columns = [
    { title: "Subscription ID", dataIndex: "_id", key: "_id" },
    {
      title: "Product",
      dataIndex: ["productId", "productname"],
      key: "product",
    },
    { title: "Frequency", dataIndex: "frequency", key: "frequency" },
    {
      title: "Start Date",
      dataIndex: "startDate",
      key: "startDate",
      render: (date) => new Date(date).toLocaleDateString(),
    },
    {
      title: "Last Delivery Date",
      dataIndex: "deliveryData",
      key: "deliveryData",
      render: (deliveryData) => {
        if (!deliveryData || deliveryData.length === 0) {
          return "No deliveries yet"; // Handle empty deliveryData
        }



        // Format the latest date
        return deliveryData[deliveryData.length - 1]?.date

      },
    },
    { title: "Status", dataIndex: "status", key: "status" },
    {
      title: "Actions",
      key: "actions",
      render: (text, record) =>
        record.status !== "Delivered" ? (
          <Button
            onClick={() => showModal(record)}
            style={{ backgroundColor: "#000", color: "white" }}
          >
            Pause Subscription
          </Button>
        ) : record.status == "paused" ? (
          <Button
            onClick={() => handleContinue(record)}
            style={{ backgroundColor: "#000", color: "white" }}
          >
            Continue Subscription
          </Button>
        ) : null,
    },
  ];
  const today = new Date();
  const tomorrow = new Date(today);
  const showSubscriptionDetails = (subscription) => {
    Modal.info({
      title: `Subscription Details - ${subscription._id}`,
      content: (
        <div>
          <Descriptions bordered column={2} layout="vertical">
            <Descriptions.Item label="Product">
              {subscription.productId?.productname}
            </Descriptions.Item>
            <Descriptions.Item label="Frequency">
              {subscription.frequency}
            </Descriptions.Item>
            <Descriptions.Item label="Quantity">
              {subscription.quantity}
            </Descriptions.Item>
            <Descriptions.Item label="Start Date">
              {new Date(subscription.startDate).toLocaleDateString()}
            </Descriptions.Item>
            <Descriptions.Item label="End Date">
              {subscription.endDate
                ? new Date(subscription.endDate).toLocaleDateString()
                : "N/A"}
            </Descriptions.Item>
            <Descriptions.Item label="Total Orders">
              {subscription.totalOrders}
            </Descriptions.Item>
            <Descriptions.Item label="Total Quantity">
              {subscription.totalQuantity}
            </Descriptions.Item>
            <Descriptions.Item label="Product Value">
              {subscription.selectedWeight.price?.toFixed(2)}
            </Descriptions.Item>
            {/* <Descriptions.Item label="Discount">
              {subscription.discount?.toFixed(2)}
            </Descriptions.Item> */}
            {/* <Descriptions.Item label="Subtotal">
              {subscription.subtotal?.toFixed(2)}
            </Descriptions.Item> */}
            <Descriptions.Item label="Delivery Charges">
              {subscription.deliveryCharges?.toFixed(2)}
            </Descriptions.Item>
            <Descriptions.Item label="Total">
              {subscription.productValue?.toFixed(2)}
            </Descriptions.Item>
          </Descriptions>

          <h4>Shipping Address:</h4>
          <Descriptions bordered column={1}>
            <Descriptions.Item label="Full Name">
              {subscription.shippingAddress?.fullName
                ? subscription.shippingAddress?.fullName
                : subscription.shippingAddress?.name}
            </Descriptions.Item>
            <Descriptions.Item label="Address">
              {subscription.shippingAddress?.street
                ? subscription.shippingAddress?.street
                : subscription.shippingAddress?.address1}
              ,{" "}
              {subscription.shippingAddress?.city
                ? subscription.shippingAddress?.city
                : subscription.shippingAddress?.address2}{" "}
              {subscription.shippingAddress?.state ? "," : ""}{" "}
              {subscription.shippingAddress?.state},{" "}
              {subscription.shippingAddress?.pincode}
            </Descriptions.Item>
            <Descriptions.Item label="Phone">
              {subscription.shippingAddress?.number
                ? subscription.shippingAddress?.number
                : subscription.shippingAddress?.mobile}
            </Descriptions.Item>
            <Descriptions.Item label="Email">
              {subscription.shippingAddress?.email}
            </Descriptions.Item>
          </Descriptions>

          <h4>Selected Weight:</h4>
          <Descriptions bordered column={1}>
            <Descriptions.Item label={`Weight`}>
              {subscription.selectedWeight?.value}{" "}
              {subscription.selectedWeight?.units}
            </Descriptions.Item>
          </Descriptions>

          {/* {subscription.dayQuantities && (
            <>
              <h4>Day Quantities:</h4>
              <Descriptions bordered column={2}>
                {Object.entries(subscription.dayQuantities).map(
                  ([day, quantity]) => (
                    <Descriptions.Item key={day} label={day}>
                      {quantity}
                    </Descriptions.Item>
                  )
                )}
              </Descriptions>
            </>
          )} */}
          {subscription.pauseDates && subscription.pauseDates.length > 0 && (
            <>
              <h4>Pause Dates:</h4>
              <Descriptions bordered column={1}>
                {subscription.pauseDates.map((date, index) => (
                  <Descriptions.Item
                    key={index}
                    label={`Pause Date ${index + 1}`}
                  >
                    {new Date(date).toLocaleDateString()}
                  </Descriptions.Item>
                ))}
              </Descriptions>
            </>
          )}

          {subscription.deliveryData && subscription.deliveryData.length > 0 && (
            <>
              <h4>Delivery History:</h4>
              <Descriptions bordered column={1}>
                {subscription.deliveryData.map((date, index) => (
                  <Descriptions.Item
                    key={index}
                    label={`Delivery Date ${index + 1}`}
                  >
                    {date.date} - {date.status}
                  </Descriptions.Item>
                ))}
              </Descriptions>
            </>
          )}

        </div>
      ),
      width: "90%",
      style: { top: 20 },
    });
  };
  return (
    <Card title="Subscriptions">
      <Space style={{ marginBottom: 16 }}>
        <Input
          placeholder="Search Subscription ID, Status, or Product"
          prefix={<SearchOutlined />}
          value={searchText}
          onChange={(e) => setSearchText(e.target.value)}
        />
        <DatePicker.RangePicker onChange={(dates) => setDateRange(dates)} />
      </Space>
      <Table
        columns={columns}
        dataSource={filteredSubscriptions}
        rowKey="_id"
        scroll={{ x: "max-content" }}
        pagination={{ pageSize: 10 }}
        locale={{
          emptyText: (
            <div className="flex flex-col items-center">
              <Empty
                image={Empty.PRESENTED_IMAGE_SIMPLE}
                description="No subscriptions found"
              />
              <Link to="/">
                <button className="bg-#f7ab27-400 rounded-full text-white duration-200 font-semibold hover:bg-#f7ab27-500 mt-4 px-4 py-2 transition-colors">
                  Continue Shopping
                </button>
              </Link>
            </div>
          ),
        }}
        expandable={{
          expandedRowRender: (record) => (
            <Button
              onClick={() => showSubscriptionDetails(record)}
              style={{ backgroundColor: "#000", color: "white" }}
            >
              View Full Details
            </Button>
          ),
        }}
      />
      <Modal
        title={`Pause Subscription ${selectedSubscription?._id}`}
        visible={isModalVisible}
        onOk={handlePause}
        onCancel={handleCancel}
        okText="Confirm Pause"
        cancelText="Cancel"
        okButtonProps={{ style: { backgroundColor: "#000", color: "#fff" } }}
      >
        <div>
          <h3>Select Pause Date</h3>
          <DatePicker
            value={pauseDate}
            onChange={(date) => setPauseDate(date)}
            format="YYYY-MM-DD"
            disabledDate={(current) =>
              current && current < moment().startOf("day")
            }
          />
        </div>
        <div className="p-4 mt-6 bg-gray-100 rounded-lg shadow-md">
          <h3 className="mb-3 text-lg font-semibold text-gray-800">Pause Date List</h3>
          {selectedSubscription?.pauseDates?.length > 0 ? (
            <ul className="space-y-2 list-none">
              {selectedSubscription.pauseDates.map((date, index) => {
                const isPast = moment(date).isBefore(moment(), "day");
                return (
                  <li
                    key={index}
                    className="flex items-center justify-between p-2 bg-white rounded-md shadow-sm"
                  >
                    <div className="flex items-center gap-2">
                      <span className="font-medium text-gray-600">{index + 1}.</span>
                      <span className="text-gray-800">{moment(date).format("DD/MM/YYYY")}</span>
                    </div>
                    {!isPast && (
                      <button
                        onClick={() => handleRemove(date)}
                        className="font-medium text-red-500 hover:text-red-700"
                      >
                        Remove
                      </button>
                    )}
                  </li>
                )
              })}
            </ul>
          ) : (
            <p className="italic text-gray-500">No pause dates available.</p>
          )}
        </div>
      </Modal>
    </Card>
  );
}

export default Account;
